*{
    margin:0;
    padding: 0;
}

.boxes{
    border:1px solid maroon;
    border-radius: 5%;
    padding: 30px;
    background-color: white;
}
.fielder{
    color: white !important;
    padding: 5px;
}
.rejbtn{
  position: relative;
  margin-left: 90px !important;
}
.profileBox{
    width: 30%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 10px;
    row-gap: 10px;
    column-gap: 10px;
    margin-left: 30%;
}
.calcev{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
}
.calan{
    position: relative;
    border :2px maroon; 
    border-style: none;
    border-radius: 15%;
    padding: 10px;
}
.calcevContainer {
    position: relative;
  }
  
  .calcev .demo {
    position: absolute;
    bottom: 10px;
    right: 10px;
  }
  
  .newbtnss {
    position: absolute;
    top: 10%;
    right: 5%; /* Adjust as needed */
    transform: translateY(-50%);
  }
  
  @media screen and (max-width: 600px) {
    .calcev .demo,
    .newbtnss {
      position: static;
      margin-top: 30px; /* Add margin for better spacing */
      text-align: right;
    }
  }
  .calcevContainer {
  position: relative;
}

.calcev .demo {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.newbtnss {
  position: absolute;
  top: 10%;
  right: 5%; /* Adjust as needed */
  transform: translateY(-50%);
}

@media screen and (max-width: 600px) {
  .calcev .demo,
  .newbtnss {
    position: static;
    margin-top: 40px; /* Add margin for better spacing */
    text-align: right;
  }
}
.cardContent {
  display: flex;
  align-items: center;
}

.avatar {
  margin-right: 16px; /* Adjust the margin as needed */
}

.empName {
  font-size: 1.2rem;
  font-weight: 500;
}

.cardsdesign{
    background-color: maroon  !important;
    color: white !important;
    font-family: sans-serif;
    font-weight: bold;
    border-radius: 15% !important;
    padding: 10px;
    opacity: 90%;
}